/* eslint-disable no-loop-func */

import { observable, action, decorate } from 'mobx';

class Pokemon {

    countPokemon = 40;
    poketArray = [];
    array = [];
    sizeView = 10;
    clickOnPokemon = false;
    tempPokemon = [];
    pageCount = 80;
    typePokemon = ['bug', 'dark', 'dragon', 'electric', 'fairy', 'fighting',
        'fire', 'flying', 'ghost', 'grass', 'ground', 'ice', 'normal', 'poison',
        'psychic', 'rock', 'steel', 'water'];
    filterArray = {};
    fullArray = require('../database.json');
    filterPoketArray = [];
    renderFilter = [];
    selectedPage = 0;
    searchValue = false;

    changeFilterPoketArray(props) {
        this.filterPoketArray = props;
    }

    changeRenderFilter(props) {
        this.renderFilter = [...this.renderFilter, props];
    }

    setFiter() {
        // console.log('фільтруєм', this.fullArray);
        let newArray = this.fullArray.filter((e) => {
            // console.log(e);
            for (var j = 0; j < e[3].length; j++) {
                for (let key in this.filterArray) {
                    if (this.filterArray[key]) {//key - значение текстовое,  this.filterArray[key] - значение ключа
                        if (e[3][j].type.name === key) {
                            // console.log(e[0][j].type.name, key);
                            return true;
                        }

                    }
                }
            }
            return false;
        });
        this.filterPoketArray = JSON.parse(JSON.stringify(newArray));
        // console.log('відфільтрували', newArray);
    }

    clearFilter() {
        this.filterArray = {};
        this.filterPoketArray = [];
        this.renderFilter = [];
    }

    changePageCount(countPage) {
        this.pageCount = countPage;
    }

    changeFilterArray(filter) {
        if (this.filterArray[filter] === undefined) {
            this.filterArray[filter] = true;
        } else if (this.filterArray[filter] === true) {
            this.filterArray[filter] = false;
        } else {
            this.filterArray[filter] = true;
        }
    }

    pushPokemon(poket) {
        this.poketArray = [...this.poketArray, poket];
        // this.poketArray.push(poket);
    }

    changePoketArray(props) {
        // console.log('renderFilter', this.renderFilter.length);
        this.poketArray = JSON.parse(JSON.stringify(props));
    }

    clickPokemon(poket) {
        this.clickOnPokemon = poket;
    }

    changeTempPokemon(poket) {
        this.tempPokemon = poket;
    }

    pokemonLoad() {
        this.countPokemon += 10;
    }

    pushArray(temp) {
        this.array = [...this.array, temp];
        // this.array.push(temp);
    }

    changeArray(temp) {
        this.array = temp;
    }

    changeSizeView(temp) {
        this.sizeView = +temp;
    }

    changeAll() {
        // this.countPokemon = countPokemon;
        this.poketArray = [];
        this.array = [];
    }
    changeSelectedPage(props) {
        // console.log('change selected page', props);
        this.selectedPage = props;
    }

    changeSearchValue(props) {
        this.searchValue = props;
    }

}
decorate(Pokemon, {
    changeFilterPoketArray: action,
    changeSearchValue: action,
    searchValue: observable,
    changeSelectedPage: action,
    selectedPage: observable,
    loadFullPokemon: action,
    setFiter: action,
    clearFilter: action,
    changePageCount: action,
    changeFilterArray: action,
    pushPokemon: action,
    changePoketArray: action,
    clickPokemon: action,
    changeTempPokemon: action,
    pokemonLoad: action,
    pushArray: action,
    changeArray: action,
    changeSizeView: action,
    changeAll: action,
    changeRenderFilter: action,
    countPokemon: observable,
    poketArray: observable,
    array: observable,
    sizeView: observable,
    clickOnPokemon: observable,
    tempPokemon: observable,
    pageCount: observable,
    typePokemon: observable,
    filterArray: observable,
    fullArray: observable,
    filterPoketArray: observable,
    renderFilter: observable,
});

const pokemon = new Pokemon();

export default pokemon;
export { Pokemon };