import React from 'react';
import { Route } from "react-router-dom";
import { observer, inject } from "mobx-react";
import ReactPaginate from 'react-paginate';

import '../style/mainPage.css';
import '../style/pagination.css';
import '../style/search.css';
import Beta from './pokemonBeta.jsx';
import Pokemon from './pokemon';

window.onload = () => {
    window.location.href = "#/page/1";
}

const Poke = inject('pokemon')(observer(
    function Pokedex(props) {
        let handleSort = () => {
            let pokemons = props.pokemon.fullArray;
            pokemons = pokemons.sort(function (a, b) {
                var nameA = a[1].toLowerCase(), nameB = b[1].toLowerCase()
                if (nameA < nameB) //сортируем строки по возрастанию
                    return -1
                if (nameA > nameB)
                    return 1
                return 0 // Никакой сортировки
            })
            props.pokemon.changeFilterPoketArray(pokemons);
            props.pokemon.changeAll();
            props.pokemon.changePageCount(pokemons.length / props.pokemon.sizeView);
            pokemons.slice(0, 10).map(item => {
                return handlePush(item);
            });
            props.history.push(`/page/1`);
            props.pokemon.changeSelectedPage(0);
        }

        let handleChangeSizeView = (e) => {
            props.history.push(`/page/1`);
            props.pokemon.changeSelectedPage(0);
            props.pokemon.changeSizeView(e.target.value);
            props.pokemon.changeAll();

            if (props.pokemon.filterPoketArray.length === 0) {
                props.pokemon.changePageCount(800 / e.target.value);
                getPokemon(1, e.target.value);
            }
            else {
                props.pokemon.changePageCount(props.pokemon.filterPoketArray.length / e.target.value);
                // console.log('handlePageClick');
                let temp = props.pokemon.filterPoketArray.filter((element, index) => {
                    if (index + 1 >= 0 && index + 1 <= e.target.value) {
                        return true;
                    }
                    return false;
                });
                temp.forEach((e) => {
                    handlePush(e);
                })
            }
        }

        let getPokemon = (a, b) => {
            // console.log('get pokemon', a, b);
            for (var i = a; i <= b; i++) {
                let temp = props.pokemon.fullArray[i - 1];
                handlePush(temp);
            }
        }

        let handlePush = (value) => {
            if (value !== undefined) {
                let tempGrid = [];
                props.pokemon.pushPokemon(value);
                tempGrid.push(<Pokemon prop={value} key={value[1]} click={props.history} />)
                props.pokemon.pushArray(tempGrid);
                localStorage.setItem(JSON.stringify(value[1]), JSON.stringify(value));
            }
        }

        let handlePageClick = data => {
            // console.log(data);
            let selected = data.selected + 1;
            props.history.push(`/page/${selected}`);
            props.pokemon.changeAll();
            if (props.pokemon.filterPoketArray.length === 0)
                getPokemon((props.pokemon.sizeView * selected) - props.pokemon.sizeView + 1, selected * props.pokemon.sizeView);
            else {
                let temp = props.pokemon.filterPoketArray.filter((e, index) => {
                    if (index + 1 >= (props.pokemon.sizeView * selected) - props.pokemon.sizeView + 1
                        && index + 1 <= selected * props.pokemon.sizeView) {
                        return true;
                    }
                    return false;
                });

                temp.forEach((e) => {
                    handlePush(e);
                })
            }
            props.pokemon.changeSelectedPage(data.selected);
        }

        let popUp = () => {
            document.getElementById('popup').className = 'popup--active';
            // document.getElementById('sidebar').setAttribute("style", "position: absolute; top: -200px; transition: 3s;");
        }

        let popUpHide = () => {
            if (document.getElementById('popup').classList.contains("popup--active")) {
                document.getElementById('popup').className = 'popup';
            }
        }

        let changeFilter = (value) => {
            props.pokemon.changeFilterArray(value);
        }

        let setFilter = () => {
            props.pokemon.setFiter();
            var flag = false;
            for (let key in props.pokemon.filterArray) {
                if (props.pokemon.filterArray[key]) {
                    flag = true;
                }
            }
            if (flag === false) {
                clearFilter();
            }
            else if (props.pokemon.filterPoketArray.length !== 0) {

                props.pokemon.changeAll();
                props.pokemon.changePageCount(props.pokemon.filterPoketArray.length / props.pokemon.sizeView);
                for (let i = 0; i < 10; i++) {
                    handlePush(props.pokemon.filterPoketArray[i]);
                }
                props.history.push(`/page/1`);
                props.pokemon.changeSelectedPage(0);
            }
            if (document.getElementById('popup').classList.contains("popup--active")) {
                document.getElementById('popup').className = 'popup';
            }
        }

        let clearFilter = () => {
            props.history.push(`/page/1`);
            props.pokemon.changeAll();
            props.pokemon.changePageCount(800 / props.pokemon.sizeView);
            getPokemon(1, props.pokemon.sizeView);
            props.pokemon.clearFilter();
            props.pokemon.changeSelectedPage(0);
        }

        let changeSearch = () => {
            let temp = document.getElementById('searchInput').value;
            let pokemons = props.pokemon.fullArray;
            pokemons = pokemons.filter(item => {
                if (item[1].indexOf(temp.toLowerCase()) !== -1) {
                    return true;
                }
                return false;
            });
            props.pokemon.changeFilterPoketArray(pokemons);
            props.pokemon.changeAll();
            props.pokemon.changePageCount(pokemons.length / props.pokemon.sizeView);
            pokemons.slice(0, 10).map(item => {
                return handlePush(item);
            });
            props.history.push(`/page/1`);
            props.pokemon.changeSelectedPage(0);

        }
            return (
                <div className="container">
                    <div id="sidebar">
                        <div className="header">
                            <span>Pokedex</span>
                            <div className='headerButton'>
                                <div className="d7" id='desktop-search'>
                                    <form>
                                        <input type="text" placeholder="Искать здесь..." onChange={changeSearch} id='searchInput' />
                                        <button type="submit"></button>
                                    </form>
                                </div>
                                <select name="sizeView" className='fas buttonMenu' id="sizeView" size="1" onChange={handleChangeSizeView}>
                                    <option value="10" >10</option>
                                    <option value="20" >20</option>
                                    <option value="50" >50</option>
                                </select>
                                <i className="fas fa-sort-alpha-down" onClick={handleSort}></i>
                                <i className="fas fa-filter" onClick={popUp} ></i>
                                <i className="fas fa-broom" onClick={clearFilter}></i>
                            </div>
                        </div>
                    </div>
                    <div className="d7" id='mobile-search'>
                        <form>
                            <input type="text" placeholder="Искать здесь..." onChange={changeSearch} id='searchInput' />
                            <button type="submit"></button>
                        </form>
                    </div>
                    <div id='mainPage'>
                        <div className='popup' id='popup'>
                            <div className='bodyPopup'>
                                <div className='headers' ><span onClick={popUpHide}>x</span></div>
                                <div className='bodyFilter'>
                                    <ul className='filterContainer'>
                                        {props.pokemon.typePokemon.map(key => {
                                            return <li className='filterButton' key={key}>
                                                <span className='filterName'>{key}</span>
                                                <span className={(props.pokemon.filterArray[key] === true) ? 'selectFiter--Active' : 'selectFilter'} onClick={() => changeFilter(key)}> &#10010; </span>
                                            </li>
                                        })}
                                    </ul>

                                </div>
                                <div className='setFilter'>
                                    <button onClick={setFilter}> set filter </button>
                                </div>

                            </div>
                        </div>
                        <div className='pokemonItems'>
                            <Route path="/page/:numberPage" component={Beta} />
                        </div>
                        <div className='alignItem'>
                            <ReactPaginate
                                previousLabel={'<'}
                                nextLabel={'>   '}
                                breakLabel={'...'}
                                breakClassName={'break-me'}
                                pageCount={props.pokemon.pageCount}
                                marginPagesDisplayed={2}
                                pageRangeDisplayed={2}
                                onPageChange={handlePageClick}
                                containerClassName={'pagination'}
                                subContainerClassName={'pages pagination'}
                                activeClassName={'active'}
                                forcePage={props.pokemon.selectedPage}
                            />
                        </div>
                    </div>
                </div>
            );
    }
))

export default Poke;