import React from 'react';
import { withRouter } from 'react-router-dom';
import '../style/pokemonInfo.css';
import { observer, inject } from "mobx-react";

const PokeInfo = inject('pokemon')(observer(

    function PokemonInfo(props) {
        let handleGoHome = () => {
            props.history.goBack();
        }
        let name = JSON.stringify(props.match.params.pokemonName);
        let pokemon = JSON.parse(localStorage.getItem(name));
        let stat = [];
        if (pokemon) {
            for (var i = 0; i < 6; i++) {
                stat.push(
                    <div className='pokemonStats' key={pokemon[2][i].stat.url}>
                        {pokemon[2][i].stat.name} - {pokemon[2][i].base_stat}
                    </div>
                );
            }
            for (var j = 0; j < pokemon[3].length; j++) {
                stat.push(
                    <div className='poketSlot' key={pokemon[3][j].type.url}>
                        {pokemon[3][j].type.name}
                    </div>
                );

            }
        } else {
            PokemonInfo.push(<div>Failed 404</div>)
        }
        props.pokemon.changeTempPokemons(stat);
        return (
            <div >
                <div className='pokemonPages'>
                    <div className='pokemonImg'>
                        <span>{pokemon[1]}</span>
                        <img src={pokemon[0]} alt={pokemon[1]}></img>
                    </div>
                    <div className='pokemonInformation'>
                        {stat}
                    </div>
                </div>
                <div className='loadMore' onClick={handleGoHome}>
                    Go home
                </div>
            </div>
        );
    }
))

export default withRouter(PokeInfo)