import React, { useEffect } from 'react';
import '../style/pokemon.css';
import Pokemon from './pokemon';
import { observer, inject } from "mobx-react";// import { compose } from 'recompose';

const PokeBeta = inject('pokemon')(observer(
    function Pokedex(props) {
        useEffect(() => {
                let page = +props.match.params.numberPage;
                props.pokemon.changeAll();
                getPokemon((props.pokemon.sizeView * page) - props.pokemon.sizeView + 1, page * props.pokemon.sizeView);
        }, [])

        let handlePush = (value) => {
            if (value !== undefined) {
                let tempGrid = [];
                tempGrid.push(<Pokemon prop={value} key={value[1]} click={value.history} />)
                props.pokemon.pushArray(tempGrid);
            }
        }

        let getPokemon = (a, b) => {
            let temp = require('../database.json');
            for (var i = a; i <= b; i++) {
                handlePush(temp[i - 1]);
            }
        }
        return (
            <div className="mainPokemonsGrid" >
                {props.pokemon.clickOnPokemon ? props.pokemon.tempPokemon : <div className="FlexGrid" >{props.pokemon.array}</div>}
            </div>
        );
    }
))

export default PokeBeta