import React, { useEffect, useState } from 'react';
import '../style/pokemon.css';
import { observer, inject } from "mobx-react";

const Poke = inject('pokemon')(observer(
    function Pokemon(props) {
        let handleClick = () => {
            let stat = [];

            for (var i = 0; i < 6; i++) {
                stat.push(
                    <div className='pokemonStats' key={props.prop[2][i].stat.url}>
                        {props.prop[2][i].stat.name} - {props.prop[2][i].base_stat}
                    </div>
                );
            }

            for (var j = 0; j < props.prop[3].length; j++) {
                stat.push(
                    <div className='poketSlot' key={props.prop[3][j].type.url}>
                        {props.prop[3][j].type.name}
                    </div>
                );
            }
            const temp =
                <div className="poketInfo">
                    <div className='pokemonPages'>
                        <div className='pokemonImg'>
                            <span>{props.prop[1]}</span>
                            <img src={props.prop[0]} alt={props.prop[1]}></img>
                        </div>
                        <div className='pokemonInformation'>
                            {stat}
                        </div>
                    </div>
                    <div className='gridBack'>
                        <button onClick={handleBack} className='buttonBack'>back</button>
                    </div>
                </div>;
            props.pokemon.changeTempPokemon(temp);//new array
            props.pokemon.clickPokemon(true);

        }
        const [tempValue] = useState(
            <div className="Pokemon" onClick={handleClick} key={props.prop[1]}>
                <img src={props.prop[0]} alt='pokemon' />
                <span>{props.prop[1]}</span>
            </div>
        )
        let handleBack = () => {
            props.pokemon.clickPokemon(false);
        }

        useEffect(() => {
            localStorage.setItem(JSON.stringify(props.prop[1]), JSON.stringify(props.prop));
        }, [])

        return (
            <div key={props.prop[1]}>
                {tempValue}
            </div>
        );
    }
))

export default Poke;